  }
  .header__nav-list li:not(:last-child) {
    margin-right: 10px;
  }
  .header__nav-list li:nth-child(4) {
    margin-right: 125px;
  }
}
@media (min-width: 1200px) {
  .header__nav-list {
    padding: 30px;
    box-sizing: border-box;
    font-size: 14px;
  }
}
.header__nav-toggle {
  position: absolute;
  display: block;
  z-index: 2;
  top: 0;
  right: 0;
  border: none;
  outline: none;
  width: 40px;
  height: 40px;
  background-color: transparent;
  cursor: pointer;
}
@media (min-width: 768px) {
  .header__nav-toggle {
    display: none;
  }
}
.header__nav--closed {
  background-color: transparent;
  position: absolute;
  z-index: 4;
  top: 0;
  right: 0;
}
@media (max-width: 767px) {
  .header__nav--closed .header__nav-toggle::before {
    content: "";
    position: absolute;
    top: 10px;
    left: 8px;
    width: 25px;
    height: 2px;
    background-color: #e8c300;
    box-shadow: 0 8px 0 0 #e8c300, 0 16px 0 0 #e8c300;
  }
  .header__nav--closed .site-list__item {
    display: none;
  }
  .header__nav--opened {
    background-image: url("../img/bg.jpg");
    background-repeat: no-repeat;
    background-size: cover;
    animation-name: bounce1;
    animation-duration: 0.6s;
  }
}
.header__nav--opened .header__nav-toggle {
  top: 0;
  right: 0;
  left: auto;
}
.header__nav--opened .header__nav-toggle::before,
.header__nav--opened .header__nav-toggle::after {
  content: "";
  position: absolute;
  top: 20px;
  left: 5px;
  width: 36px;
  height: 2px;
  background-color: #e8c300;
}
.header__nav--opened .header__nav-toggle::before {
  transform: rotate(45deg);
  box-shadow: none;
}
.header__nav--opened .header__nav-toggle::after {
  transform: rotate(-45deg);
}
.header__nav--opened .header__nav-toggle:active::before,
.header__nav--opened .header__nav-toggle:active::after {
  background-color: rgba(232, 195, 0, 0.3);
}
@keyframes bounce1 {
  0% {
    transform: translateY(-400px);
  }
  70% {
    transform: translateY(-11px);
  }
  90% {
    transform: translateY(10px);
  }
  100% {
    transform: translateY(0);
  }
}
